import {prisma} from './database.server';

export async function addExpense(expenseData, userId) {
    try {
        return await prisma.expense.create({
            data: {
                title: expenseData.title,
                amount: +expenseData.amount,
                date: new Date(expenseData.date),
                User: {connect: {id: userId}}
            }
        });
    } catch (error) {
        console.log('delta-addexpense-error: ', error);
        throw new Error('Failed to add expense');
    }

}

export async function getExpenses(userId) {
    if (!userId) {
        throw new Error('Failed to get expenses');
    }
    try {
        const expenses = await prisma.expense.findMany({
            where: {userId},
            orderBy: {date: 'desc'}
        });
        return expenses
    } catch (error) {
        console.log('delta-getExpenses-error: ', error);
        throw new Error('Failed to get expenses');
    }
}

export async function getExpense(id) {
    try {
        const expense = prisma.expense.findFirst({where: {id: id}});
        return expense;
    } catch (error) {
        console.log('delta-getSingleExpense-error: ', error);
        throw new Error('Failed to get expense');
    }
}

export async function updateExpense(id, expenseData) {
    try {
        await prisma.expense.update({
            where: {id},
            data: {
                title: expenseData.title,
                amount: +expenseData.amount,
                date: new Date(expenseData.date)
            }
        });
    } catch (error) {
        console.log('delta-updateExpense-error: ', error);
        throw new Error('Failed to update expense');
    }
}

export async function deleteExpense(id) {
    try {
        await prisma.expense.delete({
            where: {id}
        });
    } catch (error) {
        console.log('delta-deleteExpense-error: ', error);
        throw new Error('Failed to delete expense');
    }
}
